import { useNavigation } from "@react-navigation/native";
import React from "react";
import { FlatList } from "react-native-web";
import EBurmese from "../elements/EBurmese";
import EOffcanvas from "../elements/EOffcanvas";
import BookTitle from "./BookTitle";

export default function BookTitleList({ data }) {
  const navigation = useNavigation();

  const renderItem = React.useCallback(
    ({ item, index }) => (
      <BookTitle item={item} index={index} navigation={navigation} />
    ),
    []
  );

  const keyExtractor = React.useCallback((item) => item.title, []);

  return (
    <EOffcanvas id={"bookTitles"}>
      <EOffcanvas.Header>
        <EBurmese src={"စာအုပ္ေခါင္းစဥ္မ်ား"} />
      </EOffcanvas.Header>
      <EOffcanvas.Body>
        <FlatList
          data={data}
          renderItem={renderItem}
          keyExtractor={keyExtractor}
          contentContainerStyle={{ paddingBottom: 69 }}
        />
      </EOffcanvas.Body>
    </EOffcanvas>
  );
}
