import { useNavigation } from "@react-navigation/native";
import React from "react";
import BookTitleList from "~/common/components/BookTitleList";
import Feed from "~/common/components/Feed";
import Nav from "~/common/components/Nav";
import EAnimatedFlatList from "~/common/elements/EAnimatedFlatList";
import useBooks from "~/utils/hooks/useBooks";

export default function Home() {
  const books = useBooks();

  // Renderer
  const navigation = useNavigation();

  const renderItem = React.useCallback(
    ({ item }) => <Feed item={item} navigation={navigation} name={"Book"} />,
    []
  );

  return (
    <>
      <EAnimatedFlatList
        data={books.data}
        renderItem={renderItem}
        NavComponent={<Nav />}
      />
      {books.data.length > 0 && <BookTitleList data={books.data} />}
    </>
  );
}
