import React from "react";
import EBurmese from "../elements/EBurmese";
import MenuButton from "./MenuButton";
import ZawgyiUnicodeConverter from "./ZawgyiUnicodeConverter";

export default function Nav({
  title = "FourBooks",
  headerRight = <ZawgyiUnicodeConverter />,
  headerLeft,
}) {
  return (
    <nav
      style={{ height: 64, zIndex: 99999 }}
      className={"navbar bg-primary navbar-dark shadow"}
    >
      <div className="container-fluid">
        {headerLeft || <MenuButton />}
        <div className="navbar-brand fw-bold">
          <EBurmese src={title} />
        </div>
        {headerRight}
      </div>
    </nav>
  );
}
