import React from "react";
import { getIntroDataAsync } from "../api/books";
import useStorage from "./useStorage";

const initialState = {
  data: [],
  error: false,
};

export default function useBooks() {
  const [state, setState] = React.useState(initialState);
  const { getItem, setItem } = useStorage("@books");

  // On start of component
  const setData = React.useCallback((data) => {
    setState((prev) => ({
      ...prev,
      data: data,
    }));
  }, []);

  const getInitialData = React.useCallback(async () => {
    const item = getItem();
    const res = await getIntroDataAsync(item);

    if (res) {
      setData(res.data);
      return setItem(res);
    }

    return setItem((prev) => ({ ...prev, error: true }));
  }, []);

  React.useEffect(() => {
    getInitialData();
  }, []);

  return { data: state.data, error: state.error, getInitialData };
}
