import React from "react";
import EBurmese from "../elements/EBurmese";

export default class BookTitle extends React.PureComponent {
  constructor() {
    super();
    this.handlePress = this.handlePress.bind(this);
  }

  handlePress() {
    this.props.navigation.navigate("BookPost", {
      title: this.props.item?.title,
      writer: this.props.item?.writer || "credit to original writer",
      serverId: this.props.item?.serverId,
    });
  }

  render() {
    const text = this.props.index + 1 + ". " + this.props.item?.title;

    return (
      <div className="p-2 border-bottom border-1" onClick={this.handlePress}>
        <EBurmese src={text} />
      </div>
    );
  }
}
