import React from "react";
import EBurmese from "~/common/elements/EBurmese";

// TODO: Dark Theme???
export default class Feed extends React.PureComponent {
  constructor() {
    super();
    this.handlePress = this.handlePress.bind(this);
  }

  handlePress() {
    this.props.navigation.navigate(
      this.props.route === "Favorite" ? "FavoritePost" : "BookPost",
      {
        title: this.props.item?.title,
        writer: this.props.item?.writer || "credit to original writer",
        serverId: this.props.item?.serverId,
      }
    );
  }

  render() {
    const { item } = this.props;

    return (
      <div
        className="card p-2"
        onClick={this.handlePress}
        style={{
          backgroundColor: "inherit",
          color: "inherit",
          cursor: "pointer",
        }}
        title={`click to read more about ${item?.title}`}
      >
        <div className="card-title">
          <EBurmese src={item?.title} />
        </div>
        <div className="card-subtitle fs-italic">
          {item?.writer || "credit to original writer"}
        </div>
        <div className="card-text">
          <EBurmese src={item?.intro} />
        </div>
      </div>
    );
  }
}
