import { db, store } from "~/assets/firebaseConfig";

const getIntroDataAsync = async (data) => {
  try {
    const raw = await store.collection("Dashboard").doc("Intro").get();
    const res = raw.data();

    return res.timestamp === data?.timestamp ? data : res;
  } catch (error) {
    console.log(error.code);
    return data;
  }
};

const getStoryDataAsync = async (serverId) => {
  try {
    const res = await db.ref().child("Books").child(serverId).get();

    return res.val();
  } catch (error) {
    console.log(error);
    return null;
  }
};

export { getIntroDataAsync, getStoryDataAsync };
