import { useScrollToTop } from "@react-navigation/native";
import React from "react";
import { Animated } from "react-native";
import { SafeAreaView } from "react-native-safe-area-context";
import ELoading from "~/common/elements/ELoading";

export default function EAnimatedFlatList({
  data = [],
  renderItem,
  separatorHeight = 8,
  headerHeight = 64,
  NavComponent,
  keyExtractor,
}) {
  // Scroll To Top
  const ref = React.useRef(null);
  useScrollToTop(ref);

  const getKeys = React.useCallback((_, index) => index.toString(), []);

  const Separator = React.useCallback(
    () => <div style={{ height: separatorHeight }} />,
    [separatorHeight]
  );

  // Show & Hide Navigation On scroll
  const scrollY = React.useRef(new Animated.Value(0)).current;
  const clampedScrollY = scrollY.interpolate({
    inputRange: [0, 1],
    outputRange: [0, 1],
    extrapolateLeft: "clamp",
  });
  const scrollYClamped = Animated.diffClamp(clampedScrollY, 0, headerHeight);
  const y = scrollYClamped.interpolate({
    inputRange: [0, headerHeight],
    outputRange: [0, -headerHeight],
  });
  const onScroll = Animated.event(
    [{ nativeEvent: { contentOffset: { y: scrollY } } }],
    { useNativeDriver: true }
  );

  if (data.length === 0) {
    return <ELoading />;
  }

  return (
    <SafeAreaView style={{ flex: 1 }}>
      <Animated.FlatList
        ref={ref}
        contentContainerStyle={{ padding: 8, paddingTop: headerHeight + 8 }}
        ItemSeparatorComponent={Separator}
        data={data}
        renderItem={renderItem}
        keyExtractor={keyExtractor || getKeys}
        onScroll={onScroll}
        scrollEventThrottle={16}
      />
      <Animated.View
        style={{
          transform: [{ translateY: y }],
          position: "absolute",
          top: 0,
          start: 0,
          end: 0,
          height: headerHeight,
        }}
      >
        {NavComponent}
      </Animated.View>
    </SafeAreaView>
  );
}
