import React from "react";

export default function EOffcanvas({ children, className, id }) {
  return (
    <div
      className={"offcanvas offcanvas-start " + className}
      tabIndex="-1"
      id={`${id}`}
      aria-labelledby={`${id}Label`}
    >
      {children}
    </div>
  );
}

const Header = ({ className, children }) => (
  <div className={"offcanvas-header " + className}>
    <h5 className="offcanvas-title" id="offcanvasExampleLabel">
      {children}
    </h5>
    <button
      type="button"
      className="btn-close"
      data-bs-dismiss="offcanvas"
      aria-label="Close"
    ></button>
  </div>
);

const Body = ({ children, className }) => (
  <div className={"offcanvas-body " + className}>{children}</div>
);

const Control = ({ id, children, className }) => (
  <div
    className={className}
    data-bs-toggle="offcanvas"
    data-bs-target={`#${id}`}
    aria-controls={id}
  >
    {children}
  </div>
);

EOffcanvas.Control = Control;
EOffcanvas.Header = Header;
EOffcanvas.Body = Body;
